.profile__info {
    display: grid;
    grid-template-columns: 3fr 9fr;
    grid-template-areas:
  "photo data data";
    grid-gap: 15px;
}

.profile__data {
    grid-area: data;
    margin-bottom: 120px;
}

.profile__photo {
    grid-area: photo;
    position: relative;
    min-width: 200px;
    width: 200px;
    height: 200px;
    background-color: #e5e5e5;
    border-radius: 5px;
}

.profile__photo img {
    display: block;
    max-width: 100%;
    width: 100%;
    height: auto;
    object-fit: cover;
}

.profile__name {
    font-size: 24px;
    font-weight: 700;
}

.profile__item, .profile__name {
    margin-bottom: 10px;
}

.profile__btn-upload {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 30px;
    background-color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

.profile__btn-upload span {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #686b6d;
    font-size: 13px
}

.profile__btn-upload span::before {
    content: '';
    background: url('../../../assets/images/icons/upload.svg') center center no-repeat;
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

input[id=upload-photo] {
    width: 1px;
    height: 1px;
    opacity: 0;
}